import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import Menus from '../views/menus'//点餐
import Order from '../views/Order'//订单
// import qrCode from '../views/qr'//二维码


export default new Router({
  scrollBehavior(to, from, positoned) {
    if (positoned) {
      return positoned
    } else {
      return {x: 0, y: 0}
    }
  },
  base: '/client/',
  routes: [
    {
      path: '/',
      redirect: '/shop/1/menus'
    },
    {path: '/shop/:deskId/menus', component: Menus, name: 'menus', meta: {keepAlive: true}},
    {path: '/shop/:deskId/order', component: Order, name: 'order', meta: {keepAlive: false}},
    // { path: '/shop/qrCode', component: Qr, name: 'qrCode', meta: { keepAlive: false } },
  ]
})



