<template>
  <div class="steper">
	 <div class="step">
      <button class="minus el-icon-remove" v-if="pioneerNum" @click="useMinus"></button>
      <span v-if="pioneerNum">{{pioneerNum}}</span>
      <button class="plus el-icon-circle-plus" @click="usePlus"></button>
    </div>
	</div>
</template>

<script>
export default {
  name: 'operater',
  props: ['selectData','pioneerNum'],
  methods: {
  	useMinus(){
      if(this.pioneerNum > 0){
        this.$store.commit('decNum',this.selectData)
      }
  	},
    usePlus(){
      this.$store.commit('incNum',this.selectData)
    }
  },
  watch: {
    pioneerNum(val){
      return val
    }
  }
}
</script>

<style lang="css" scoped>
.step {
  position: absolute;
  right: .4rem;
  bottom: .4rem;
  width: 2.2rem;
  height: .666rem;
  background: #fff;
}
.step button {
  position: absolute;
  top: 0;
  width: .666rem;
  height: .666rem;
  border: 0;
}
.step .plus {
  right: 0;
  font-size: 0.9rem;
  color: rgb(255, 192, 1);
  background: none;
}
.step .minus {
  left: 0;
   font-size: 0.9rem;
  color: rgb(255, 192, 1);
  background: none;
}
.step span {
    position: absolute;
    left: .8rem;
    top: 0.1rem;
    height: .666rem;
    line-height: .666rem;
    text-align: center;
    width: .82rem;
    font-size: 0.56rem;
}
</style>



