<template>
  <div class="menus">
         <div class="header">您的桌号是: {{deskId}}</div>
	<div class="list">
		<div class="left">
			<ul class="category">
				<li v-for="(list,idx) in categoryList" :key="idx" :class="{on:idx==activeId}" @click="getpioneer(list.menu_id,idx)">{{list.menu_name}}</li>
			</ul>
		</div>
		<div class="right">
			<ul class="pioneer">
				<li v-for="(pioneer,idx) in pioneerList" :key="pioneer.menu_id + '_' +pioneer.food_id" v-show="pioneer.menu_id == categoryid">
					<img @click="getPopup(idx)" :src="'/image/' + pioneer.food_imgid">
					<div class="intro">
						<h3>{{pioneer.food_name}}</h3>
						<p class="price"><span>¥{{pioneer.food_price}}</span>／份</p>
					</div>
					<operater :selectData="pioneer" :pioneerNum="pioneer.num"></operater>
				</li>
			</ul>
		</div>
	</div>
	<cart></cart>
	<popup v-if="showPopup" :popupId="showPopup"></popup>
  </div>
</template>

<script>
import Cart from '../components/Cart'
import Operater from '../components/Operater'
import Popup from '../components/Popup'

export default {
  name: 'menus',
  data () {
    return {
      deskId: this.$route.params.deskId,
      showPopup: 0,
      activeId: 0,
      categoryList: [],
      categoryid: 0
    }
  },
  created: function () {
    
      
  },
  beforeMount:function (){
    document.title = '下单'
   var that = this;
    var loadingInstance = this.$Loading.service({ fullscreen: true });
    that.$ajax.get(that.$ajaxUrl.menus)
    .then(function (response) {
          that.$nextTick(() => {
            loadingInstance.close();
        });
       if(response && response.data ) {
           var res = response.data;
           if(res && res.code == 0) {
               that.categoryList = res.data
               that.categoryid = _.first(res.data).menu_id;
                that.$store.commit('init',res.data)
                that.$store.commit('initId', that.deskId)
           }
       }
    })
    .catch(function (error) {
        console.log(error);
          that.$nextTick(() => {
            loadingInstance.close();
        });
    });

    this.$root.$on('change-popup', (val)=>{
      this.showPopup = val;
    })
    
   
  },
  computed: {
    pioneerList(){
      return this.$store.state.orderList
    }
  },
  methods: {
  	getpioneer(id,idx){
  	  this.categoryid = id
      this.activeId = idx
  	},
  	getPopup(id){
  	  this.showPopup = ++id
  	}
  },
  components: {
  	Cart,
  	Popup,
    Operater
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="css" scoped>
@import '../style/menus';

</style>



