import Vue from 'vue'
import Vuex from 'vuex'
import _ from 'lodash'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        orderList: [],
        deskNum:'',
        orderData: {}
    },
    mutations: {
        init: (state, order) => {
            var SList = [];
            order.forEach(ele => {
                ele.foods.forEach(food =>{
                    food.menu_id = ele.menu_id
                    SList.push(food)
                })
            });
            state.orderList = SList
        },
        initId: (state, deskId) => {
            state.deskNum = deskId
        },
        incNum: (state, order) => {
            var index = _.findIndex(state.orderList, function (o) {
                return o.food_id == order.food_id
            });

            if (index != -1) {
                var num = state.orderList[index].num
                num ? state.orderList[index].num++ : state.orderList[index].num = 1
            }
            state.orderList = _.filter(state.orderList, function () { return true })
        },
        decNum: (state, order) => {
            var index = _.findIndex(state.orderList, function (o) {
                return o.food_id == order.food_id
            });

            if (index != -1) {
                state.orderList[index].num--
            }
            state.orderList = _.filter(state.orderList, function () { return true })
        },
        orderData: function (state, orderData) {
            state.orderData.no = orderData.no;
            state.orderData.time = orderData.time;
        },
        clear: (state) => {
            var list = [];
            _.forEach(state.orderList, function (val) {
                if (val.num) val.num = 0
                list.push(val)
            });

            state.orderList = list
        },
    },
    getters: {
        deskNum: state => {
            return state.deskNum
        },
        cartList: state => {
            return _.filter(state.orderList, 'num')
        },
        totalNum: state => {
            var total = 0
            _.forEach(state.orderList, function (val) {
                if (val.num) total += val.num
            });

            return total
        },
        totalFee: state => {
            var total = 0
            _.forEach(state.orderList, function (val) {
                if (val.num) total += val.food_price * val.num
            });

            return total.toFixed(2)
        },
        detailData: (state) => (food_id) => {
            return _.find(state.orderList, function (o) { return o.food_id == food_id })
        },
        orderData: state => {
            return state.orderData
        }
    }
})


