<template>
  <div class="order">
    <div class="header">
      <div class="hid">
        <p>您的桌号是:{{deskNum}}</p>
        <p>您的订单号是：{{getOrderData.no}}</p>
        <p>{{getOrderData.time}}</p>
        <!-- <p><span>来自店内订单</span></p> -->
      </div>
    </div>
    <div class="card">
      <div class="food">
        <div class="title">
          <div class="f1">商品</div>
          <div class="f2">数量</div>
          <div class="f3">金额</div>
        </div>
        <ul>
          <li v-for="(cart, idx) in cartData" :key="idx">
            <div class="f1">{{cart.food_name}}</div>
            <div class="f2">{{cart.num}}</div>
            <div class="f3">￥{{cart.food_price}}</div>
          </li>
        </ul>
      </div>
      <ul class="tinfo">
        <li>
          <div class="left">总数量</div>
          <div class="right">{{totalNum}}</div>
        </li>
        <li>
          <div class="left">桌号</div>
          <div class="right">{{deskNum}}</div>
        </li>
        <li>
          <div class="left">支付方式</div>
          <div class="right">等待支付</div>
        </li>
        <li>
          <div class="left">订单金额</div>
          <div class="right">￥{{totalFee}}</div>
        </li>
        <li>
          <div class="left">实付金额</div>
          <div class="right red">￥{{totalFee}}</div>
        </li>
        <li>
          <div class="left">备注</div>
          <div class="right"></div>
        </li>
        <!-- <li>
                  <div class="left">地址</div>
                  <div class="right"></div>
              </li> -->
      </ul>
    </div>
    <div class="bottom">
      <div class="btn" @click="success">付款</div>
      <div class="btn" @click="open2">取消</div>
      <div class="btn b2" @click="success">确定</div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'order',
    beforeMount() {
      document.title = '订单详情'
    },
    computed: {
      cartData() {
        return this.$store.getters.cartList
      },
      totalNum() {
        return this.$store.getters.totalNum
      },
      totalFee() {
        return this.$store.getters.totalFee
      },
      deskNum() {
        return this.$store.getters.deskNum
      },
      getOrderData() {
        return this.$store.getters.orderData
      }
    },
    methods: {
      cancel() {
        var that = this;
        that.$ajax.post(that.$ajaxUrl.cancel, JSON.stringify({no: that.getOrderData.no}))
          .then(function (response) {
            if (response && response.data) {
              var res = response.data;
              if (res && res.code == 1) {
                that.$message({
                  type: 'success',
                  message: '删除成功!'
                });
                setTimeout(() => {
                  that.$store.commit('clear');
                  that.$router.go(-1);
                }, 1000);

              } else {
                that.$message({
                  type: 'error',
                  message: '删除失败，请重试!'
                });
              }
            }
          })
          .catch(function (error) {
          });
      },
      success() {
        this.$ajax.post('/pioneer/order/pay', {deskId: '1'})
          .then(function (response) {
            console.log(response);
          })
          .catch(function (error) {
          });
        this.$message({
          type: 'success',
          message: 'ok!'
        });
      },
      open2() {
        this.$confirm('此操作将删除该订单, 是否继续?', '提示', {
          showCancelButton: true,
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'error'
        }).then(() => {
          this.cancel();
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="css" scoped>
  .order {
    height: 100%;
  }

  .header {
    padding: .8rem .8rem .8rem 2.8rem;
    position: relative;
  }

  .header img {
    position: absolute;
    width: 1.36rem;
    height: 1.16rem;
    top: 1.4rem;
    left: .8rem;
  }

  .hid {
    font-size: .48rem;
  }

  .hid p {
    margin-bottom: .2rem;
  }

  .hid p span {
    color: #ff0000;
  }

  .card {
    width: 9.6rem;
    margin: 0 .2rem;
    border-radius: 8px 8px 0 0;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, .08), -2px -2px 5px rgba(0, 0, 0, .08);
    max-height: 76%;
    overflow: scroll;
    padding-bottom: 1.466rem;
  }

  .food {
    padding: .4rem;
    border-bottom: 1px solid #eee;
  }

  .food .title, .food li {
    overflow: hidden;
  }

  .food .title {
    color: #fa0;
    font-size: .48rem;
  }

  .food li {
    font-size: .42rem;
  }

  .food .f1 {
    float: left;
    width: 40%;
    height: 1.2rem;
    line-height: 1.2rem;
    text-align: left;
  }

  .food .f2 {
    float: left;
    width: 20%;
    height: 1.2rem;
    line-height: 1.2rem;
    text-align: center;
  }

  .food .f3 {
    float: left;
    width: 40%;
    height: 1.2rem;
    line-height: 1.2rem;
    text-align: right;
  }

  .tinfo {
    padding: .5rem .4rem;
    font-size: .42rem;
  }

  .tinfo li {
    overflow: hidden;
    padding: .2rem 0;
  }

  .tinfo .left {
    width: 30%;
    float: left;
  }

  .tinfo .right {
    width: 70%;
    float: left;
    text-align: right;
  }

  .tinfo .right.red {
    color: #ff0000;
    font-size: .48rem;
  }

  .bottom {
    position: absolute;
    width: 9.6rem;
    height: 1.466rem;
    line-height: 1.466rem;
    text-align: center;
    background-color: #fff;
    bottom: 0;
    left: .2rem;
    border-top: 1px solid #eee;
  }

  .btn {
    display: inline-block;
    height: .8rem;
    line-height: .75rem;
    padding: 0 .6rem;
    border: 1px solid #fa0;
    border-radius: 20px;
    color: #fa0;
    background-color: #fff;
  }

  .btn.b2 {
    background-color: #fa0;
    color: #fff;
  }
</style>



