<template>
  <div class="popup">
    <div @click="getPopup" class="mask"></div>
    <div class="layer">
      
      <div class="swipe-wrapper">
        <swipe class="my-swipe" :defaultIndex="popupId-1" :auto="0">
        <swipe-item class="swipe-item" v-for="(pioneer, idx) in pioneerList" :key="idx" :style="{backgroundImage: 'url(/image/'+ pioneer.food_imgid+')'}">
          <h3>{{pioneer.food_name}}</h3>
          <div class="info">
            <div class="price">
              <p>¥{{pioneer.food_price}}</p>
            </div>
            <operater :selectData="pioneer" :pioneerNum="pioneer.num"></operater>
             <div class="detail">
                {{pioneer.food_desc}}
            </div>
          </div>
           
        </swipe-item>
        </swipe>
      </div>
    </div>
  </div>
</template>

<script>
import 'vue-swipe/dist/vue-swipe.css'
import { Swipe, SwipeItem } from 'vue-swipe'
import Operater from '../components/Operater'

export default {
  name: 'popup',
  props: ['popupId'],
  data () {
    return {
      bannerList: [
        'http://via.placeholder.com/340x200',
        'http://via.placeholder.com/340x200',
        'http://via.placeholder.com/340x200'
      ]
    }
  },
  methods: {
    getPopup(){
      this.$root.$emit('change-popup',0)
    }
  },
  computed: {
    pioneerList(){
      return this.$store.state.orderList
    }
  },
  components: {
    Swipe,
    SwipeItem,
    Operater
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="css" scoped>
.mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,.4);
}
.layer {
  position: fixed;
  top: 14%;
  left: 0;
  width: 100%;
  background-color: #fff;
}
.layer h3 {
  position: absolute;
  top: -1.05rem;
  width: 100%;
  text-align: center;
  left: 0;
  color: #fff;
  font-size: .48rem;
}
.swipe-wrapper {
  padding: 1.5rem 0;
  background-color: rgba(0,0,0,.9);
}
.my-swipe {
  height: 6rem;
  color: #fff;
  text-align: center;
  overflow: visible!important;
}
.swipe-item {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.info {
  background-color: #fff;
  height: 3rem;
  position: absolute;
  width: 100%;
  left: 0;
  top: 7.5rem;
}
.price {
  position: absolute;
  color: #ff0000;
  top: .2rem;
  left: .4rem;
}
.price p {
  font-size: .5rem;
}
.price span {
  color: #bbb;
  font-size: .32rem;
}
.btn {
  display: inline-block;
  height: .8rem;
  line-height: .75rem;
  padding: 0 .6rem;
  border: 1px solid #3788ff;
  border-radius: 20px;
  color: #3788ff;
  background-color: #fff;
  position: absolute;
  right: .4rem;
  top: .5rem;
}
.btn.b2 {
  background-color: #3788ff;
  color: #fff;
}
.my-swipe .detail{
    width: 100%;
    max-height: 1.7rem;
    overflow: hidden;
    margin-top: 1rem;
    font-size: 0.4rem;
    text-align: left;
    padding: 0.2rem 0.4rem;
    color: #3788ff;
    display: flex;
    align-items: center;
}
</style>
<style lang="css">
.my-swipe .mint-swipe-items-wrap {
  overflow: visible!important;
}
.my-swipe .step {
  position: absolute;
  right: .3rem;
  top: .1rem;
  width: 2.2rem;
  height: .666rem;
  background: #fff;
}
.my-swipe .step span {
  color: #333;
}
</style>



