<template>
  <div class="cart">
    <div class="scart">
      <div class="icon" @click="getOrderList('1')"></div>
      <div class="total">
        <div class="not" v-if="!totalNum">未选购商品</div>
        <div class="has" v-if="totalNum">
          <p>订单数量：{{totalNum}}</p>
          <p>总金额：<span>¥{{totalFee}}</span></p>
        </div>
      </div>
      <div class="btn" @click="goOrder">下单</div>
    </div>
    <div class="mask" @click="getOrderList('0')" :class="{on: isActive==1}"></div>
    <div class="orderlist" :class="{on: isActive==1}">
      <div class="title">购物车<span class="del" @click="clearList"></span></div>
      <ul>
        <li v-for="(cart, idx) in cartData" :key="idx">
          <div class="name">{{cart.food_name}}</div>
          <div class="price">¥{{cart.food_price}}</div>
          <operater :selectData="cart" :pioneerNum="cart.num"></operater>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import Operater from '../components/Operater'

  export default {
    name: 'cart',
    data() {
      return {
        isActive: 0
      }
    },
    computed: {
      cartData() {
        return this.$store.getters.cartList
      },
      totalNum() {
        return this.$store.getters.totalNum
      },
      totalFee() {
        return this.$store.getters.totalFee
      },
      deskNum() {
        return this.$store.getters.deskNum
      }
    },
    methods: {
      getOrderList(status) {
        if (this.cartData.length == 0) {
          this.isActive = 0
          return
        }
        this.isActive = status
      },
      clearList() {
        this.$store.commit('clear')
      },
      goOrder() {
        var that = this;
        if (this.totalNum == 0) {
          alert('请先点餐');
          return
        }
        var loadingInstance = this.$Loading.service({fullscreen: true});
        var params = {
          deskId: this.deskNum,
          orderList: this.cartData,
          totalNum: this.totalNum,
          totalFee: this.totalFee
        }
        that.$ajax.post(that.$ajaxUrl.cart, JSON.stringify(params))
          .then(function (response) {
            that.$nextTick(() => {
              loadingInstance.close();
            });
            if (response && response.data) {
              var res = response.data;
              if (res && res.code == 1 && res.data && res.data.no) {
                that.$store.commit('orderData', res.data)
                that.$router.push({name: 'order', params: {deskId: that.deskNum}})
              }
            }
          })
          .catch(function (error) {
            that.$nextTick(() => {
              loadingInstance.close();
            });
          });
      }
    },
    components: {
      Operater
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="css" scoped>
  .mask {
    opacity: 0;
    transition: all .4s;
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, .7);
    z-index: -1;
    top: 0;
    left: 0;
  }

  .mask.on {
    opacity: 1;
    z-index: 7;
  }

  .orderlist {
    position: fixed;
    z-index: 8;
    width: 9.6rem;
    left: .2rem;
    bottom: 1.8rem;
    background: #fff;
    border-radius: 8px 8px 0 0;
    transform: translateY(100%);
    transition: all .4s;
  }

  .orderlist.on {
    transform: translateY(0);
  }

  .orderlist .title {
    position: relative;
    height: 1rem;
    background: #cecfd0;
    color: #666;
    padding: 0 .4rem;
    line-height: 1rem;
    border-radius: 8px 8px 0 0;
    font-size: .4rem;
  }

  .orderlist .title .del {
    position: absolute;
    width: .6rem;
    height: .6rem;
    background-size: contain;
    top: .2rem;
    right: .4rem;
  }

  .orderlist ul {
    min-height: 4rem;
    max-height: 7.5rem;
    overflow: scroll;
    padding-bottom: .8rem;
  }

  .orderlist li {
    position: relative;
    padding: .4rem;
    border-bottom: 1px solid #eee;
  }

  .orderlist li .name {
    width: 3.3rem;
    font-size: .4rem;
    display: inline-block;
  }

  .orderlist li .price {
    width: 3rem;
    font-size: .4rem;
    display: inline-block;
    color: #ff0000;
  }

  .scart {
    position: fixed;
    z-index: 9;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1.8rem;
    background: #fa0;
    color: #fff;
  }

  .scart .icon {
    position: absolute;
    width: 1.6rem;
    height: 1.6rem;
    font-size: 0.9rem;
    color: #83aee8;
    background: url(../assets/cart.png) no-repeat 0 0;
    background-size: contain;
    top: 0rem;
    left: .6rem;
  }

  .scart .total {
    position: absolute;
    left: 2.8rem;
    top: 0;
    width: 3.566rem;
    height: 1.8rem;
    border-right: 1px solid #83aee8;
    padding: .266rem 0;
  }

  .scart .total .not {
    line-height: 1.268rem;
    text-align: center;
  }

  .scart .total .has {
    font-size: .36rem;
  }

  .scart .total .has p:first-child {
    margin-bottom: .1rem;
  }

  .scart .total .has span {
    color: #ff0000;
    font-size: .5rem;
  }

  .scart a {
    color: #fff;
  }

  .scart .btn {
    position: absolute;
    top: 0;
    right: 0;
    width: 3.6rem;
    text-align: center;
    height: 1.8rem;
    line-height: 1.8rem;
    font-size: .5rem;
    letter-spacing: 5px;
  }
</style>



